.header__nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.header__image {
  width: 100%;
  height: 40vw;
  background: linear-gradient(-5deg, #3f3f3f 15%, transparent 15%),
    url("images/meals.jpg") no-repeat center center / cover;
}
